import React, { useState, useEffect } from "react";
import { useGetAboutQuery } from "@/redux/api/about_us";

// –û–ø—Ä–µ–¥–µ–ª—è–µ–º —Ç–∏–ø—ã –¥–ª—è –ø—Ä–æ–ø—Å–æ–≤ –∫–æ–º–ø–æ–Ω–µ–Ω—Ç–∞ StatItem
interface StatItemProps {
  icon: React.ReactNode; // icon –º–æ–∂–µ—Ç –±—ã—Ç—å –ª—é–±—ã–º –æ—Ç–æ–±—Ä–∞–∂–∞–µ–º—ã–º —ç–ª–µ–º–µ–Ω—Ç–æ–º, –Ω–∞–ø—Ä–∏–º–µ—Ä, —ç–º–æ–¥–∑–∏ –∏–ª–∏ –∏–∫–æ–Ω–∫–æ–π
  value: number;
  label: string;
}

const StatItem: React.FC<StatItemProps> = ({ icon, value, label }) => {
  const [count, setCount] = useState(0);

  useEffect(() => {
    const duration = 2000;
    const steps = 50;
    const increment = value / steps;
    let current = 0;
    const timer = setInterval(() => {
      current += increment;
      if (current >= value) {
        clearInterval(timer);
        setCount(value);
      } else {
        setCount(Math.floor(current));
      }
    }, duration / steps);
    return () => clearInterval(timer);
  }, [value]);

  return (
    <div className="flex flex-col items-center p-4 bg-white rounded-lg shadow-md transition-all duration-300 hover:shadow-lg hover:-translate-y-1">
      <div className="text-4xl text-blue-600 mb-2">{icon}</div>
      <div className="text-3xl font-bold text-gray-800 mb-1">{count}</div>
      <div className="text-sm text-gray-600">{label}</div>
    </div>
  );
};

const AboutUsContent = () => {
  const { data } = useGetAboutQuery();

  const stats = [
    { icon: "üéì", value: data?.[0]?.students || 6395, label: "–°—Ç—É–¥–µ–Ω—Ç—ã" },
    {
      icon: "üë®‚Äçüéì",
      value: data?.[0]?.graduates_per_year || 13283,
      label: "–í—ã–ø—É—Å–∫–Ω–∏–∫–∏",
    },
    { icon: "üë®‚Äçüíº", value: 330, label: "–°–æ—Ç—Ä—É–¥–Ω–∏–∫–∏" },
    {
      icon: "üèõÔ∏è",
      value: data?.[0]?.count_books || 85,
      label: "–£—á–µ–±–Ω—ã–µ –õ–∞–±–æ—Ä–∞—Ç–æ—Ä–∏–∏, –ú–∞—Å—Ç–µ—Ä—Å–∫–∏–µ –ò –°—Ç—É–¥–∏–∏",
    },
  ];

  return (
    <div className="container mx-auto px-4 py-8">
      <h1 className="text-3xl font-bold text-center text-gray-800 mb-8">
        –ö–ì–£ "–ú–∞–Ω–∞—Å" –≤ —Ü–∏—Ñ—Ä–∞—Ö
      </h1>
      <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-6">
        {stats.map((stat, index) => (
          <StatItem key={index} {...stat} />
        ))}
      </div>
      <div className="text-center mt-8">
        <button className="bg-red-600 text-white px-6 py-2 rounded-full hover:bg-red-700 transition-colors duration-300">
          –ü–æ–¥—Ä–æ–±–Ω–µ–µ
        </button>
      </div>
    </div>
  );
};

export default AboutUsContent;
